import React from "react";
import {
  ConfigProvider,
  AdaptivityProvider,
  AppRoot,
  SplitLayout,
  SplitCol,
  View,
  Panel,
  PanelHeader,
  SliderSwitch,
  FormItem,
  FormLayoutGroup,
  Group,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import MainScr from "./elements/main";

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      whenuserwantstosleep: undefined,
    };
  }

  addToState(name, value) {
    let tstate = this.state;
    tstate[name] = value;
    this.setState(tstate);
    return this.state;
  }

  render() {
    return (
      <ConfigProvider>
        <AdaptivityProvider>
          <AppRoot>
            <SplitLayout header={<PanelHeader separator={false} />}>
              <SplitCol>
                <View activePanel="main">
                  <Panel id="main">
                    <PanelHeader>Sleepy</PanelHeader>
                    <Group>
                      <FormLayoutGroup mode="vertical">
                        <FormItem>
                          <SliderSwitch
                            onSwitch={(value) =>
                              this.addToState("whenuserwantstosleep", value)
                            }
                            options={[
                              {
                                name: "Я засыпаю сейчас",
                                value: "now",
                              },
                              {
                                name: "Я засыпаю потом",
                                value: "later",
                              },
                            ]}
                          />
                        </FormItem>
                        <FormItem style={{ paddingTop: 0 }}>
                          <MainScr when={this.state.whenuserwantstosleep} />
                        </FormItem>
                      </FormLayoutGroup>
                    </Group>
                  </Panel>
                </View>
              </SplitCol>
            </SplitLayout>
          </AppRoot>
        </AdaptivityProvider>
      </ConfigProvider>
    );
  }
}

export default App;
