import {
  SliderSwitch,
  Group,
  Placeholder,
  ContentCard,
  Text,
} from "@vkontakte/vkui";
import { Icon56MoonOutline } from "@vkontakte/icons";
import React from 'react';

// eslint-disable-next-line
String.prototype.toMinute = function (char) {
  if (this.length < 2) {
    return "0" + this;
  } else {
    return this;
  }
};

class MainScr extends React.Component {
  constructor(props) {
    super(props);

    this.state = { updated: false };
  }

  render() {
    if (this.props.when === "later") {
      return (
        <Group>
          <SliderSwitch
            options={[
              {
                name: "Я засну в...",
                value: "sleep",
              },
              {
                name: "Я проснусь в...",
                value: "wake",
              },
            ]}
          />
        </Group>
      );
    } else if (this.props.when === "now") {
      setInterval(() => {
        this.setState({ updated: true });
      }, 200);
      var date = Date.now() + (new Date().getTimezoneOffset() / -60) * 1000;
      return (
        <div>
          <Text weight="regular" style={{ marginBottom: 12 }}>
            Вам стоит проснуться в:
          </Text>
          <ContentCard
            mode="outline"
            caption="1 час 30 минут"
            text={
              new Date(date + (60 + 30) * 60000).getHours().toString() +
              ":" +
              new Date(date + (60 + 30) * 60000)
                .getMinutes()
                .toString()
                .toMinute()
            }
            subtitle="1 цикл"
            style={{ marginBottom: 12 }}
          />
          <ContentCard
            mode="outline"
            caption="3 часа"
            text={
              new Date(date + 60 * 3 * 60000).getHours().toString() +
              ":" +
              new Date(date + 60 * 2 * 60000).getMinutes().toString().toMinute()
            }
            subtitle="2 цикла"
            style={{ marginBottom: 12 }}
          />
          <ContentCard
            mode="outline"
            caption="4 часа 30 минут"
            text={
              new Date(date + 270 * 60000).getHours().toString() +
              ":" +
              new Date(date + 270 * 60000).getMinutes().toString().toMinute()
            }
            subtitle="3 цикла"
            style={{ marginBottom: 12 }}
          />
          <ContentCard
            mode="outline"
            caption="6 часов"
            text={
              new Date(date + 360 * 60000).getHours().toString() +
              ":" +
              new Date(date + 360 * 60000).getMinutes().toString().toMinute()
            }
            subtitle="4 цикла"
            style={{ marginBottom: 12 }}
          />
          <ContentCard
            mode="outline"
            caption="7 часов 30 минут"
            text={
              new Date(date + 450 * 60000).getHours().toString() +
              ":" +
              new Date(date + 450 * 60000).getMinutes().toString().toMinute()
            }
            subtitle="5 циклов"
            style={{ marginBottom: 12 }}
          />
          <ContentCard
            mode="outline"
            caption="9 часов"
            text={
              new Date(date + 540 * 60000).getHours().toString() +
              ":" +
              new Date(date + 540 * 60000).getMinutes().toString().toMinute()
            }
            subtitle="6 циклов"
            style={{ marginBottom: 12 }}
          />
          <ContentCard
            mode="outline"
            caption="10 часов 30 минут"
            text={
              new Date(date + 630 * 60000).getHours().toString() +
              ":" +
              new Date(date + 630 * 60000).getMinutes().toString().toMinute()
            }
            subtitle="7 циклов"
            style={{ marginBottom: 12 }}
          />
          <ContentCard
            mode="outline"
            caption="12 часов"
            text={
              new Date(date + 720 * 60000).getHours().toString() +
              ":" +
              new Date(date + 720 * 60000).getMinutes().toString().toMinute()
            }
            subtitle="8 циклов"
            style={{ marginBottom: 12 }}
          />
        </div>
      );
    } else {
      return (
        <Placeholder icon={<Icon56MoonOutline />} header={"Сейчас или потом?"}>
          Выберите, когда вы собираетесь заснуть с помощью переключателя сверху
        </Placeholder>
      );
    }
  }
}

export default MainScr;
